import searchResultArticle from '../../components/searchResultArticle.vue';
export default {
	data() {
		return {
			hotList: {},
			searchKeyWords: '',
			searchedArticleList: {},
			searching: true,
			searchHisKeyList: uni.getStorageSync('search_history'),
			
			userInfo: this.getGlobalUserInfo(),
		}
	},
	components: {
		searchResultArticle,
	},
	onLoad: function() {
		// 查询热搜词
		var that = this;
		uni.request({
			url: that.$serverUrl + '/article/hot',
			method: "POST",
			success: (res) => {
				console.log(res);
				that.hotList = res.data.data;
				console.log(this.hotList);
			}

		})
	},
	
	methods: {
		search: function(res) {
			var that = this;
			var isSaveRecord = 1;
			
			// console.log(that.searchKeyWords);
			if (this.searchKeyWords == '' || this.searchKeyWords == null){
				uni.showToast({
					title: '搜索内容不能为空',
					duration: 1000,
					icon:'none',
				})
				return;
			}
			
			
			uni.getStorage({
				key:'search_history',
				success(res){
					let list = res.data;
					console.log(list);
					if(list.length > 10){
						for(let item of list){
							if(item == that.searchKeyWords){
								return false;
							}
						}
						list.pop();
						list.unshift(that.searchKeyWords);
					} else {
						for(let item of list){
							if(item == that.searchKeyWords){
								return false;
							}
						}
						list.unshift(that.searchKeyWords);
					}
					that.searchHisKeyList = list;
					uni.setStorage({
						key:'search_history',
						data: that.searchHisKeyList
					})
				},
				fail() {
					that.searchHisKeyList = [];
					that.searchHisKeyList.push(that.searchKeyWords);
					uni.setStorage({
						key:'search_history',
						data: that.searchHisKeyList
					});
				}
			});
			
			uni.request({
				url: that.$serverUrl + '/article/searchArticleYANG?isSaveRecord=' + isSaveRecord,
				method: "POST",
				data: {
					articleContent: that.searchKeyWords,
					userId: that.userInfo.id,
				},
				success: function(result) {
					console.log(result.data);
					that.searchedArticleList = result.data.data.rows;
					that.searching=false;
				}
			})
		},
		searchCancle: function(searching){
				this.searching = !searching;
				console.log(this.searching);
		},
		exitSearch(){
			this.hotList="",
			this.searchKeyWords="",
			this.searchedArticleList="",
			this.$emit("exitSearchSignal",0)
		},
	}
}
